import logo from './logo.svg';
import './App.css';
import { Modal, message, Input, Button, Card, Col, Row } from 'antd';
import React, { useState, useEffect } from 'react'
import ReactDom from 'react-dom'
import webSocket from 'socket.io-client'
import _ from 'lodash';

function App() {
  const [ws,setWs] = useState(null)
  const [username, setUsername] = useState('');
  const [roomname, setRoomname] = useState('');
  const [room, setRoom] = useState({});
  const [showHome, setShowHome] = React.useState(true);
  const [showGame, setShowGame] = React.useState(false);

  const createRoom = () => {
    ws.emit('create', {username, roomname});
  }

  const joinRoom = () => {
    ws.emit('join', {username, roomname});
  }

  const step = (event) => {
    const {who, host, guest, roomname} = room;
    const index = event.target.name;
    if(room.end) return message.error(``);
    if(room[who] !== username) return message.error(`"${room[who]}"`);
    ws.emit('step', {who, index});
  }

  const restartRoom = () => {
    if(!room.end) return message.error(``);
    ws.emit('restart', {username, roomname});
  }

  const leaveRoom = () => {
    ws.emit('leave', roomname);
  }

  const useMountEffect = (fun) => useEffect(fun, [])
  useMountEffect(()=>{
    setWs(webSocket('ws://35.185.152.249:3101'));
  })

  useEffect(()=>{
      if(ws){
          // console 
          console.log('success connect!')
          //
          initWebSocket()
      }
  },[ws])

  useEffect(()=>{
      if(room.end){
        if(room[room.end] === username) {
          Modal.success({
            content: '',
          });
        } else {
          Modal.error({
            content: '10',
          });
        }
      }
  },[room])

  const initWebSocket = () => {
    ws.on('create', room => {
      setRoom(room);
      setShowHome(false);
      setShowGame(true);
    })

    ws.on('join', room => {
      setRoom(room);
      setShowHome(false);
      setShowGame(true);
    })

    ws.on('step', room => {
      setRoom(room);
    })

    ws.on('restart', room => {
      setRoom(room);
    })
    
    ws.on('leave', data => {
      setShowHome(true);
      setShowGame(false);
    })
    
    ws.on('end', room => {
      setRoom(room);
    })
    
    ws.on('error', data => {
      message.error(data);
    })
  }
    
  return (
  <div className="site-card-wrapper">
    {showHome ? <Home 
      username={username} 
      setUsername={setUsername} 
      roomname={roomname} 
      setRoomname={setRoomname} 
      createRoom={createRoom} 
      leaveRoom={leaveRoom}
      joinRoom={joinRoom}
    /> : null}
    {showGame ? (
      <Row align="middle">
        <Col span={24}>{room.count === 1 ? '' : `"${room[room.who]}"`}</Col>
        <Col span={8}>
          <Row gutter={[24, 24]}>
            <Col span={24} >
              <Button name="0" onClick={step}>{room.step[0] ? room.step[0].who === 'host' ? 'O' : 'X' : '  '}</Button>
              <Button name="1" onClick={step}>{room.step[1] ? room.step[1].who === 'host' ? 'O' : 'X' : '  '}</Button>
              <Button name="2" onClick={step}>{room.step[2] ? room.step[2].who === 'host' ? 'O' : 'X' : '  '}</Button>
            </Col>
            <Col span={24} >
              <Button name="3" onClick={step}>{room.step[3] ? room.step[3].who === 'host' ? 'O' : 'X' : '  '}</Button>
              <Button name="4" onClick={step}>{room.step[4] ? room.step[4].who === 'host' ? 'O' : 'X' : '  '}</Button>
              <Button name="5" onClick={step}>{room.step[5] ? room.step[5].who === 'host' ? 'O' : 'X' : '  '}</Button>
            </Col>
            <Col span={24} >
              <Button name="6" onClick={step}>{room.step[6] ? room.step[6].who === 'host' ? 'O' : 'X' : '  '}</Button>
              <Button name="7" onClick={step}>{room.step[7] ? room.step[7].who === 'host' ? 'O' : 'X' : '  '}</Button>
              <Button name="8" onClick={step}>{room.step[8] ? room.step[8].who === 'host' ? 'O' : 'X' : '  '}</Button>
            </Col>
          </Row>
        </Col>
        <Col span={16}>
            <Input addonBefore=": "  style={{ width: 'calc(100% - 200px)' }} disabled value={room.roomname} />
            <Input addonBefore=": "  style={{ width: 'calc(100% - 200px)' }} disabled value={room.host} />
            <Input addonBefore=": "  style={{ width: 'calc(100% - 200px)' }} disabled value={room.guest} />
        </Col>
        <Col span={24}>
            <Button type="primary" onClick={restartRoom}></Button>
            <Button type="primary" onClick={leaveRoom}></Button>
        </Col>
      </Row>
    ):null}
    
  </div>
  );
}

export default App;

const Home = ({username, setUsername, roomname, setRoomname, createRoom, leaveRoom, joinRoom}) => {
  return  (
    <Row align="middle">
      <Col span={24}>
        <Row>
          <Col span={24}>
            <Card title="" bordered={false}>
              <Input.Group compact>
            <Input addonBefore=""  style={{ width: 'calc(100% - 200px)' }} placeholder="" value={username} onChange={event => setUsername(event.target.value)} />
            <Input addonBefore=""  style={{ width: 'calc(100% - 200px)' }} placeholder="" value={roomname} onChange={event => setRoomname(event.target.value)} />
              </Input.Group>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
              <Button type="primary" onClick={createRoom}></Button>
              <Button type="primary" onClick={joinRoom}></Button>
          </Col>
        </Row>
      </Col>
    </Row>
  )
}